html {
  body {
    .press-timeline {
      background: #f2f2f2;
      width: 100%;
      position: relative;
      &::after {
        content: '';
        position: absolute;
        height: 1px;
        background: #000;
        width: 100%;
        top: 50%;
      }
      .circle {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        line-height: 48px;
        text-align: center;
        background: #000;
        position: relative;
        z-index: 8;
        transition: all .2s ease-in-out;
        -webkit-transition: all .2s ease-in-out;
        -moz-transition: all .2s ease-in-out;
        cursor: pointer;
        i {
          // color: #fff;
          // font-size: 1.4rem;
        }
        &:hover {
          background: #fff;
          border: 1px solid #000;
          i {
            color: #000;
          }
        }
      }
      img {
        position: relative;
        z-index: 5;
        cursor: pointer;
      }
      .content {
        padding: 0px 30px;
      }
      .container {
        // height: 315px;
      }
      .box {
        position: relative;
        z-index: 5;
        background: #f2f2f2;
        border-left: 1px solid #000;
        border-right: 1px solid #000;
        padding: 0px 40px;
      }
    }
  }
}